*{
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior:smooth;
 
}
body,ul, li, p {
  margin: 0px;
  padding: 0px;
  list-style: none;
  font-family: "neosans regular", "Helvetica Neue", Helvetica, Arial, Sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  
  text-align: center;
  
}


